import { EResults, IResults } from "../../interfaces";

const Results = ({ results }: { results: IResults }) => {
  if (results.status === EResults.Success) {
    return (
      <div>
        <div className="container p-3">
          <h2>Results</h2>
          <div className="d-flex justify-content-center p-3">
            <div className="p-3 mx-5 text-start  border rounded border-success">
              <h3 className="p-3">You have been enrolled!</h3>
              <h4>University name: {results.university}</h4>
              <h4>Course name: {results.course}</h4>
            </div>
          </div>
        </div>
        <h5 className="text-muted">Recruitment process has finished.</h5>
      </div>
    );
  } else if (results.status === EResults.Fail) {
    return (
      <div>
        <div className="container p-3">
          <h2>Results</h2>
          <div className="d-flex justify-content-center p-3">
            <div className="w-50 p-3 mx-5 text-center border rounded border-danger">
              <h5>
                Your scores, based on your preferences, were not sufficient to
                match you with any of the university courses.
              </h5>
            </div>
          </div>
        </div>
        <h5 className="text-muted">Recruitment process has finished.</h5>
      </div>
    );
  } else if (results.status === EResults.NotReady) {
    return (
      <div className="container p-3">
        <h2>Results</h2>
        <h5 className="text-muted">Recruitment process has not began.</h5>
      </div>
    );
  } else {
    return <></>;
  }
};

export default Results;
