import Table from "react-bootstrap/Table";
import { IMultiScore } from "../../interfaces";
import ScoresTableRow from "../shared/ScoresTableRow";

const ScoresTable = ({ scores }: { scores: Array<IMultiScore> }) => {
  return (
    <Table striped>
      <thead>
        <tr>
          <th>#</th>
          <th>Subject</th>
          <th>Basic</th>
          <th>Advanced</th>
        </tr>
      </thead>
      <tbody>
        {scores.map((score: IMultiScore, index: number) => (
          <ScoresTableRow
            key={index + 1}
            id={`${index + 1}`}
            name={score.name}
            basic={score.basic}
            advanced={score.advanced}
          />
        ))}
      </tbody>
    </Table>
  );
};

export default ScoresTable;
