import {
  Auth,
  isSignInWithEmailLink,
  sendSignInLinkToEmail,
  signInWithEmailLink,
} from "firebase/auth";
import { Database, ref } from "firebase/database";
import { useEffect, useState } from "react";
import { useObject } from "react-firebase-hooks/database";
import FbHelper from "../../firebase/dao";
import { ERoles, IdName } from "../../interfaces";

const actionCodeSettings = {
  // URL you want to redirect back to. The domain (www.example.com) for this
  // URL must be in the authorized domains list in the Firebase Console.
  url: "http://localhost:3000/signInLink",
  // This must be true.
  handleCodeInApp: true,
};

const SignIn = ({
  auth,
  setRole,
  db,
}: {
  auth: Auth;
  setRole: any;
  db: Database;
}) => {
  const [dbMods, loadingMods, errorMods] = useObject(
    ref(db, "admin/moderators")
  );
  const [mods, setMods] = useState<Array<string>>([]);
  useEffect(() => {
    if (!loadingMods && dbMods) {
      setMods(FbHelper.transformModerators(dbMods));
    }
  }, [dbMods, loadingMods]);

  const sendLink = (email: string) =>
    sendSignInLinkToEmail(auth, email, actionCodeSettings)
      .then(() => {
        window.localStorage.setItem("emailForSignIn", email);
        window.alert(
          `Link has been sent to '${email}'. Check your email and spam folder.`
        );
      })
      .catch((error) => {
        const errorMessage = error.message;
        console.error(errorMessage);
      });

  const signIn = () => {
    if (isSignInWithEmailLink(auth, window.location.href)) {
      let email = window.localStorage.getItem("emailForSignIn");
      if (!email) {
        email = window.prompt("Please provide authorized college email");
      }
      email &&
        signInWithEmailLink(auth, email, window.location.href)
          .then((result) => {
            window.localStorage.removeItem("emailForSignIn");
            console.log("Signed in with link");
            const user: IdName = {
              id: result.user.uid || "",
              name: "college",
            };
            localStorage.setItem("savedUID", result.user.uid || "");
            localStorage.setItem("savedRole", ERoles.College.toString());
            setRole(ERoles.College);
          })
          .catch((error) => {
            window.alert("Invalid link");
            console.error(error.message);
          });
    } else {
      window.alert("Check email for a newest link");
    }
  };
  const onSendEmail = () => {
    let email = window.localStorage.getItem("emailForSignIn");
    if (!email) {
      email = window.prompt("Please provide authorized college email");
    }
    if (email && mods.find((modEmail) => modEmail === email) !== undefined) {
      sendLink(email);
    } else {
      window.alert("This email is not authorized. Contact our administrators.");
    }
  };
  return (
    <div className="conitaner p-3">
      <h4 className="text-dark">For authorized colleges only</h4>
      <button className="btn btn-primary m-1" onClick={signIn}>
        Sign in
      </button>
      <button className="btn btn-secondary m-1" onClick={onSendEmail}>
        Send link
      </button>
    </div>
  );
};

export default SignIn;
