import { Navigate, useLocation } from "react-router-dom";

const PrivateRoute = ({
  children,
  auth,
  redir = "/login",
}: {
  children: JSX.Element;
  auth: boolean;
  redir?: string;
}) => {
  let location = useLocation();
  if (!auth) {
    return <Navigate to={redir} state={{ from: location }} />;
  }

  return children;
};

export default PrivateRoute;
