import React from "react";
import Table from "react-bootstrap/Table";
import Button from "react-bootstrap/Button";
import ScoresTableRow from "../shared/ScoresTableRow";
import { ICourseM, IMultiScore } from "../../interfaces";

const Major = ({
  major,
  onAddAsPref,
}: {
  major: ICourseM;
  onAddAsPref: any;
}) => {
  const handleClick = (e: React.MouseEvent<HTMLElement>) => {
    e.preventDefault();
    onAddAsPref({ courseId: major.id });
  };
  return (
    <div className="border rounded mb-5 mt-3">
      <div className="p-3 mx-5 text-start">
        <h4>{major.name}</h4>
        <p>{major.desc}</p>
      </div>
      <h5>Score multipliers</h5>
      <Table striped>
        <thead>
          <tr>
            <th>#</th>
            <th>Subject</th>
            <th>Basic</th>
            <th>Advanced</th>
          </tr>
        </thead>
        <tbody>
          {major.multipliers.map((mult: IMultiScore, index: number) => (
            <ScoresTableRow
              key={index + 1}
              id={`${index + 1}`}
              name={mult.name}
              basic={mult.basic}
              advanced={mult.advanced}
            />
          ))}
        </tbody>
      </Table>
      <h5>Capacity: {major.capacity}</h5>
      <Button className="m-3" onClick={handleClick}>
        Add to My preferences
      </Button>
    </div>
  );
};

export default Major;
