import { useEffect } from "react";
import { useNavigate } from "react-router-dom";

const Logout = () => {
  const navigate = useNavigate();

  useEffect(() => {
    setTimeout(() => {
      navigate("/login");
    }, 1800);
  });
  return (
    <div className="alert alert-secondary">
      <h4>Logged out.</h4>
    </div>
  );
};

export default Logout;
