import React, { useEffect } from "react";
import { Button, Form, Table } from "react-bootstrap";
import { ICourseM, IdName, IMultiScore } from "../../interfaces";
import ScoreForm from "../shared/ScoreForm";
import ScoresTableRow from "../shared/ScoresTableRow";

const CardCourse = ({
  course,
  subjects,
  onCourseUpdate,
  onCancel,
}: {
  course: ICourseM;
  subjects: IdName[];
  onCourseUpdate: any;
  onCancel: any;
}) => {
  const [ecourse, seteCourse] = React.useState<ICourseM>(course);

  useEffect(() => {
    seteCourse(course);
  }, [course]);
  const onNameChange = (e: any) => {
    const value = e.target.value || "";
    seteCourse({ ...ecourse, name: value });
  };

  const onDescChange = (e: any) => {
    const value = e.target.value || "";
    seteCourse({ ...ecourse, desc: value });
  };

  const onCapacityChange = (e: any) => {
    const value = Number.parseInt(e.target.value) || 0;
    seteCourse({ ...ecourse, capacity: value });
  };

  const onMultAdd = (value: IMultiScore) => {
    seteCourse({ ...ecourse, multipliers: [...ecourse.multipliers, value] });
  };

  const onSave = () => {
    if (!ecourse.name) {
      window.alert("Course name cannot be empty");
    } else if (ecourse.multipliers.length === 0) {
      window.alert("Score multipliers cannot be empty");
    } else if (!ecourse.capacity || ecourse.capacity <= 0) {
      window.alert("Course capacity must be a number greater than 0");
    } else {
      onCourseUpdate(ecourse);
    }
  };

  const onDiscard = () => {
    seteCourse(course);
  };

  const handleCancel = () => {
    seteCourse(course);
    onCancel();
  };
  return (
    <div className="card mt-3 p-3 border-light">
      <div className="card-header">
        <h5 className="mb-0">Modify selected course</h5>
      </div>
      <div className="card-body text-start">
        <Form>
          <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
            <Form.Label>Course name</Form.Label>
            <Form.Control
              type="text"
              placeholder="New course name"
              value={ecourse.name}
              onChange={onNameChange}
            />
          </Form.Group>
          <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
            <Form.Label>Description</Form.Label>
            <Form.Control
              as="textarea"
              rows={3}
              placeholder="Enter course description (optional, visible to students)"
              value={ecourse.desc}
              onChange={onDescChange}
            />
          </Form.Group>
          <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
            <Form.Label>Capacity (maximum number of students)</Form.Label>
            <Form.Control
              type="text"
              value={ecourse.capacity}
              onChange={onCapacityChange}
            />
          </Form.Group>
        </Form>
        <p>Score multipliers</p>
        <Table striped className="text-center">
          <thead>
            <tr>
              <th>#</th>
              <th>Subject</th>
              <th>Basic</th>
              <th>Advanced</th>
            </tr>
          </thead>
          <tbody>
            {ecourse.multipliers.map((mult: IMultiScore, index: number) => (
              <ScoresTableRow
                key={index + 1}
                id={`${index + 1}`}
                name={mult.name}
                basic={mult.basic}
                advanced={mult.advanced}
              />
            ))}
          </tbody>
        </Table>
        <ScoreForm
          subjects={subjects.filter((s: IdName) => {
            return (
              ecourse.multipliers.find((ss: IMultiScore) => ss.id === s.id) ===
              undefined
            );
          })}
          onSubmit={onMultAdd}
        />
        <div className="mt-5 text-center">
          <Button className="btn btn-primary mx-2 px-4" onClick={onSave}>
            Save
          </Button>
          <Button className="btn btn-secondary mx-2 px-4" onClick={onDiscard}>
            Discard
          </Button>
          <Button className="btn btn-danger mx-2 px-4" onClick={handleCancel}>
            Cancel
          </Button>
        </div>
      </div>
    </div>
  );
};

export default CardCourse;
